@import url(https://fonts.googleapis.com/css?family=Roboto:100,100i,400&display=swap);
html, body, #root {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: gray;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-evenly;
  color: white;
  font-family: 'Roboto', sans-serif;
  font-weight: 100;
}

.background {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  position: absolute;
  z-index: -1;
  -webkit-animation: fadein 1s;
          animation: fadein 1s;
}

.background.hidden {
  display: none;
}

.date {
  font-size: 80px;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.text-wrapper {
  flex: 1 1;
}

.saying {
  font-size: 45px;
  display: flex;
  align-items: center;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
  flex: 1 1;
}

.saying .text {
  font-style: italic;
}

.saying .text {
  -webkit-animation: fadein 1s;
          animation: fadein 1s;
}

.saying .text.hidden {
  display: none;
}

.arrow {
  margin: 0px 20px;
  font-weight: 400;
  padding: 0px 15px;
  border-radius: 50%;
  -webkit-user-select: none; /* Safari */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
  flex: 1 1;
}

.arrow:hover {
  cursor: pointer;
  background-color: rgba(0, 0, 0, .5);
}

.saying-item {
  flex: 1 1;
}


@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}


@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}
